  background: #2b3c4d;
  transition: transform .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: rotate(0);
}

.jp-header__inner:first-child {
  background: #88a2bc;
  transition-delay: .1s;
}

.jp-dom-loaded .jp-header__inner:first-child {
  transform: rotate(4deg);
}

.jp-header__inner:nth-child(2) {
  background: #6789aa;
  transition-delay: .2s;
}

